<template>
  <div class="row">
    <div class="col-md-12">
      <card>
        <div slot="header">
          <h5 class="title">EDIT CLEANING SERVICE</h5>
        </div>
        <form>
          <div>
            <div class="row">
              <label class="col-md-2 col-form-label offset-md-3 text-right">CLEAN DATE</label>
              <div class="col-md-2">
                <fg-input>
                  <el-date-picker
                    v-model="cleaning.datePicker"
                    type="date"
                    placeholder="Pick a day"
                    :picker-options="pickerOptions1"
                    value-format="yyyy-MM-dd"
                  ></el-date-picker>
                </fg-input>
              </div>
            </div>
            <div class="row">
              <label class="col-md-2 col-form-label offset-md-3 text-right">CLEAN TIME</label>
              <div class="col-md-2">
                <fg-input>
                  <el-time-select
                    v-model="cleaning.timePicker"
                    :picker-options="{start: '00:00',
                                                          step: '00:15',
                                                          end: '23:59'}"
                    placeholder="Select time"
                  ></el-time-select>
                </fg-input>
              </div>
            </div>
            <div class="row">
              <label class="col-md-1 col-form-label offset-md-4 text-right">STATUS</label>
              <div class="col-md-3">
                <n-radio
                  class="col-md-1 display-inline"
                  v-model="enabledRadio"
                  label="PENDING"
                >PENDING</n-radio>
                <n-radio class="col-md-1 display-inline" v-model="enabledRadio" label="DONE">DONE</n-radio>
              </div>
            </div>
            <div class="row">
              <div class="col-md-6 offset-md-4">
                <n-button
                  class="button"
                  type="info"
                  @click.native="radio_status(enabledRadio); put(); handleUpdate();"
                >Update</n-button>
                <a class="cancel">Cancel</a>
                <!-- <n-button class="button" type="danger">Cancel</n-button> -->
              </div>
            </div>
          </div>
        </form>
      </card>
    </div>
  </div>
</template>
<script>
import { DatePicker, TimeSelect } from "element-ui";
import { Radio } from "src/components";
import swal from "sweetalert2";

export default {
  components: {
    [DatePicker.name]: DatePicker,
    [TimeSelect.name]: TimeSelect,
    [Radio.name]: Radio
  },
  data() {
    return {
      id: this.$route.params.id,
      cleaning: {
        datePicker: "",
        timePicker: "",
        status: ""
      },
      enabledRadio: "DONE",
      pickerOptions1: {
        shortcuts: [
          {
            text: "Today",
            onClick(picker) {
              picker.$emit("pick", new Date());
            }
          },
          {
            text: "Yesterday",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit("pick", date);
            }
          },
          {
            text: "A week ago",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", date);
            }
          }
        ]
      },
      key_data: {
        datePicker: "",
        timePicker: "",
        status: ""
      }
    };
  },
  methods: {
    //update the data to database
    put() {
      this.$http
        .put(
          "https://admindashboard-b2ab9.firebaseio.com/cleaning/" +
            this.id +
            ".json",
          this.cleaning
        )
    },
    radio_status(er) {
      if (er == "DONE") {
        this.cleaning.status = "DONE";
      } else {
        this.cleaning.status = "PENDING";
      }
    },
    handleUpdate() {
      swal({
        title: `You updated ${this.cleaning.datePicker}`,
        buttonsStyling: false,
        type: "success",
        confirmButtonClass: "btn btn-success btn-fill"
      });
    }
  },
  created(){
    this.$http.get('https://admindashboard-b2ab9.firebaseio.com/cleaning.json').then(function(data){
      return data.json();
    }).then(function(data){
        
      //pass data to key_data object
      this.key_data = data
      //set the fields accordingly with the unique id
      this.cleaning.datePicker = this.key_data[this.id].datePicker
      this.cleaning.timePicker = this.key_data[this.id].timePicker
      this.cleaning.status = this.key_data[this.id].status
        
      //toggle radio button
      if ( this.cleaning.status == 'PENDING'){
        this.enabledRadio = 'PENDING'
      }else{
        this.enabledRadio = 'DONE'
      }
    })
  }
};
</script>
<style scoped>
.display-inline {
  display: inline !important;
}
.cancel {
  /* display: inline; */
  margin-left: 20px;
  color: red;
  text-decoration: underline !important;
}
</style>
